@import url(https://fonts.googleapis.com/css?family=Montserrat);
/* learnt how to use Google fonts in React here: 
https://stackoverflow.com/questions/40769551/how-to-use-google-fonts-in-react-js */

.Body {
	font: 20px Montserrat, sans-serif;
	color: #f5f6f7;
	line-height: 1.8;
}

.firstHeading {
	font-size: 55px;
	font-weight: bold;
}

.secondHeading,
.thirdHeading {
	font-size: 24px;
}

.pictureOfMe {
	width: 200px;
	height: 200px;

	/* creates a double-border effect */
	padding: 7px;
	border: 2.5px solid #474e5d;
	background-color: #f5f6f7;
}

Img:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

/* creates spacing between a heading and the content under that heading */
.margin {
	margin-bottom: 45px;
}

/* added spacing between containers */
.container-fluid {
	padding-top: 60px;
	padding-bottom: 60px;
}

p {
	font-size: 18px;
}

.linkIcon:hover {
	opacity: 0.7;
}

/* adds spacing between icons */
.linkIcon {
	padding: 23px;
}

/* makes the list of icons in the heading display in a horizontal row 
instead of a vertical row */
.listSocials {
	display: inline;
}

.AboutMe,
.bg-3 {
	background-color: #474e5d;
	color: #ffffff;
}

.bg-2 {
	background-color: #ffffff;
	color: #474e5d;
}

/* added spacing for the footer element */
.bg-3 {
	padding-top: 20px;
	padding-bottom: 20px;
}

span {
	font: 15px Montserrat, sans-serif;
}

